import React, { useRef, useEffect } from "react";

// Subtle hero overlay with blockchain-like symbols on left/right of title
export default function HeroNetwork({ anchorId = "landing-title" }) {
  const ref = useRef(null);

  useEffect(() => {
    const el = ref.current;
    if (!el) return;

    const update = () => {
      const nav = document.querySelector('.navigation');
      const navH = nav ? nav.offsetHeight : 0;
      const anchor = document.getElementById(anchorId) || document.querySelector('.hero-title') || document.querySelector('.page-title');
      if (!anchor) return;
      const rect = anchor.getBoundingClientRect();
      const centerY = rect.top + window.scrollY + rect.height / 2 - navH;
      // The main top group is centered at y=400 (increased from 300); align container to title center
      el.style.transform = `translateY(${Math.round(centerY - 400)}px)`;
    };
    update();
    window.addEventListener('resize', update);
    window.addEventListener('scroll', update);
    const int = setInterval(update, 300);
    return () => {
      window.removeEventListener('resize', update);
      window.removeEventListener('scroll', update);
      clearInterval(int);
    };
  }, [anchorId]);

  return (
    <div
      ref={ref}
      className="hero-network-container"
      style={{
        position: 'absolute',
        top: '0',
        left: '0',
        right: '0',
        pointerEvents: 'none',
        zIndex: 0,
        overflow: 'visible'
      }}
    >
      <svg width="100%" height="800" viewBox="0 0 1400 800" preserveAspectRatio="xMidYMid meet" style={{ display: 'block', margin: '0 auto' }}>
        <defs>
          <linearGradient id="heroChainGrad" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#7c3aed" stopOpacity="0.55" />
            <stop offset="50%" stopColor="#ec4899" stopOpacity="0.5" />
            <stop offset="100%" stopColor="#fbbf24" stopOpacity="0.55" />
          </linearGradient>
          <filter id="heroGlow">
            <feGaussianBlur stdDeviation="2" result="b" />
            <feMerge>
              <feMergeNode in="b" />
              <feMergeNode in="SourceGraphic" />
            </feMerge>
          </filter>
        </defs>

        {/* Top perimeter cubes - BIGGER around title (increased ~20%) - moved up for more vertical space */}
        <g transform="translate(700,-50)" stroke="url(#heroChainGrad)" strokeWidth="1.8" fill="none" filter="url(#heroGlow)">
          {/* Left EXTRA LARGE SQUARE cube with 5 lines */}
          <g>
            <rect x="-290" y="-50" width="120" height="120" rx="8" fill="rgba(124,58,237,0.12)" />
            <path d="M -290 -50 L -268 -66 L -148 -66 L -170 -50 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M -170 -50 L -148 -66 L -148 54 L -170 70 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-282" y="-28" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">const tx = &#123;</text>
            <text x="-282" y="-12" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">  hash: &apos;0xabc&apos;,</text>
            <text x="-282" y="4" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">  block: 42069,</text>
            <text x="-282" y="20" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">  gas: 21000</text>
            <text x="-282" y="36" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">&#125;;</text>
          </g>
          {/* Center-left LARGE rect cube */}
          <g>
            <rect x="-130" y="-30" width="130" height="60" rx="7" fill="rgba(168,85,247,0.12)" />
            <path d="M -130 -30 L -110 -44 L 20 -44 L 0 -30 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 0 -30 L 20 -44 L 20 16 L 0 30 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-122" y="-10" fill="#fbbf24" opacity="0.65" fontSize="10" fontFamily="monospace">block: 12345678</text>
            <text x="-122" y="6" fill="#fbbf24" opacity="0.65" fontSize="10" fontFamily="monospace">nonce: 42</text>
            <text x="-122" y="22" fill="#fbbf24" opacity="0.65" fontSize="10" fontFamily="monospace">timestamp: now</text>
          </g>
          {/* Top-left extra cube group - BIGGER */}
          <g>
            <rect x="-220" y="-160" width="96" height="96" rx="7" fill="rgba(124,58,237,0.12)" />
            <path d="M -220 -160 L -205 -174 L -124 -174 L -139 -160 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M -139 -160 L -124 -174 L -124 -78 L -139 -64 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-212" y="-140" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">tx.ok</text>
            <text x="-212" y="-124" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">= true</text>
          </g>
          {/* Top-right extra cube group - BIGGER */}
          <g>
            <rect x="140" y="-160" width="96" height="96" rx="7" fill="rgba(168,85,247,0.12)" />
            <path d="M 140 -160 L 155 -174 L 236 -174 L 221 -160 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 221 -160 L 236 -174 L 236 -78 L 221 -64 Z" fill="rgba(251,191,36,0.18)" />
            <text x="148" y="-140" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">sig</text>
            <text x="148" y="-124" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">0x</text>
          </g>

          {/* Center-right LARGE rect cube */}
          <g>
            <rect x="10" y="-30" width="130" height="60" rx="7" fill="rgba(124,58,237,0.12)" />
            <path d="M 10 -30 L 30 -44 L 160 -44 L 140 -30 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 140 -30 L 160 -44 L 160 16 L 140 30 Z" fill="rgba(251,191,36,0.18)" />
            <text x="18" y="-10" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">gas: 21000</text>
            <text x="18" y="6" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">wei: 1e18</text>
            <text x="18" y="22" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">gwei: 50</text>
          </g>
          {/* Right EXTRA LARGE SQUARE cube with 5 lines */}
          <g>
            <rect x="180" y="-50" width="120" height="120" rx="8" fill="rgba(168,85,247,0.12)" />
            <path d="M 180 -50 L 202 -66 L 322 -66 L 300 -50 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 300 -50 L 322 -66 L 322 54 L 300 70 Z" fill="rgba(251,191,36,0.18)" />
            <text x="188" y="-28" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">transfer(to,</text>
            <text x="188" y="-12" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">  from, value)</text>
            <text x="188" y="4" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">nonce: 7</text>
            <text x="188" y="20" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">chainId: 1</text>
            <text x="188" y="36" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">success: true</text>
          </g>
        </g>

        {/* Bottom perimeter cubes - BIGGER (increased ~20%) - moved down for more vertical space */}
        <g transform="translate(700,750)" stroke="url(#heroChainGrad)" strokeWidth="1.8" fill="none" filter="url(#heroGlow)">
          {/* Left EXTRA LARGE SQUARE cube */}
          <g>
            <rect x="-290" y="-50" width="120" height="120" rx="8" fill="rgba(124,58,237,0.12)" />
            <path d="M -290 -50 L -268 -66 L -148 -66 L -170 -50 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M -170 -50 L -148 -66 L -148 54 L -170 70 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-282" y="-28" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">emit Offering</text>
            <text x="-282" y="-12" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">(user, amt,</text>
            <text x="-282" y="4" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace"> timestamp,</text>
            <text x="-282" y="20" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace"> proofNFT)</text>
            <text x="-282" y="36" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">;</text>
          </g>
          {/* Center-left LARGE rect cube */}
          <g>
            <rect x="-130" y="-30" width="130" height="60" rx="7" fill="rgba(168,85,247,0.12)" />
            <path d="M -130 -30 L -110 -44 L 20 -44 L 0 -30 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 0 -30 L 20 -44 L 20 16 L 0 30 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-122" y="-10" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">require(bal &gt; 0,</text>
            <text x="-122" y="6" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace"> &quot;Insufficient&quot;)</text>
            <text x="-122" y="22" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">;</text>
          </g>
          {/* Center-right LARGE rect cube */}
          <g>
            <rect x="10" y="-30" width="130" height="60" rx="7" fill="rgba(124,58,237,0.12)" />
            <path d="M 10 -30 L 30 -44 L 160 -44 L 140 -30 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 140 -30 L 160 -44 L 160 16 L 140 30 Z" fill="rgba(251,191,36,0.18)" />
            <text x="18" y="-10" fill="#fbbf24" opacity="0.65" fontSize="10" fontFamily="monospace">mapping(address</text>
            <text x="18" y="6" fill="#fbbf24" opacity="0.65" fontSize="10" fontFamily="monospace"> =&gt; uint256)</text>
            <text x="18" y="22" fill="#fbbf24" opacity="0.65" fontSize="10" fontFamily="monospace">balances;</text>
          </g>
          {/* Right EXTRA LARGE SQUARE cube */}
          <g>
            <rect x="180" y="-50" width="120" height="120" rx="8" fill="rgba(168,85,247,0.12)" />
            <path d="M 180 -50 L 202 -66 L 322 -66 L 300 -50 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 300 -50 L 322 -66 L 322 54 L 300 70 Z" fill="rgba(251,191,36,0.18)" />
            <text x="188" y="-28" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">function burn</text>
            <text x="188" y="-12" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">(uint amt)</text>
            <text x="188" y="4" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">external</text>
            <text x="188" y="20" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">returns(id)</text>
            <text x="188" y="36" fill="#ec4899" opacity="0.65" fontSize="10" fontFamily="monospace">&#123;...&#125;</text>
          </g>
        </g>

        {/* Left side cubes - BIGGER - adjusted for taller hero */}
        <g transform="translate(140,300)" stroke="url(#heroChainGrad)" strokeWidth="1.8" fill="none" filter="url(#heroGlow)">
          <g>
            <rect x="-95" y="-30" width="96" height="58" rx="8" fill="rgba(124,58,237,0.12)" />
            <path d="M -95 -30 L -80 -42 L 16 -42 L 1 -30 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 1 -30 L 16 -42 L 16 16 L 1 28 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-88" y="-10" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">struct</text>
            <text x="-88" y="4" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">Token &#123;</text>
            <text x="-88" y="18" fill="#a78bfa" opacity="0.65" fontSize="10" fontFamily="monospace">uint id</text>
          </g>
          <g>
            <rect x="15" y="-30" width="96" height="58" rx="8" fill="rgba(168,85,247,0.12)" />
            <path d="M 15 -30 L 30 -42 L 126 -42 L 111 -30 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 111 -30 L 126 -42 L 126 16 L 111 28 Z" fill="rgba(251,191,36,0.18)" />
            <text x="15" y="-8" fill="#fbbf24" opacity="0.65" fontSize="9" fontFamily="monospace">string</text>
            <text x="15" y="2" fill="#fbbf24" opacity="0.65" fontSize="9" fontFamily="monospace">name;</text>
            <text x="15" y="12" fill="#fbbf24" opacity="0.65" fontSize="9" fontFamily="monospace">&#125;</text>
          </g>
        </g>

        {/* Right side cubes - adjusted for taller hero */}
        <g transform="translate(1250,300)" stroke="url(#heroChainGrad)" strokeWidth="1.8" fill="none" filter="url(#heroGlow)">
          <g>
            <rect x="-90" y="-24" width="80" height="48" rx="8" fill="rgba(124,58,237,0.12)" />
            <path d="M -90 -24 L -78 -34 L 2 -34 L -10 -24 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M -10 -24 L 2 -34 L 2 14 L -10 24 Z" fill="rgba(251,191,36,0.18)" />
            <text x="-85" y="-8" fill="#ec4899" opacity="0.65" fontSize="9" fontFamily="monospace">event</text>
            <text x="-85" y="2" fill="#ec4899" opacity="0.65" fontSize="9" fontFamily="monospace">Burn(</text>
            <text x="-85" y="12" fill="#ec4899" opacity="0.65" fontSize="9" fontFamily="monospace">uint amt</text>
          </g>
          <g>
            <rect x="0" y="-24" width="80" height="48" rx="8" fill="rgba(168,85,247,0.12)" />
            <path d="M 0 -24 L 12 -34 L 92 -34 L 80 -24 Z" fill="rgba(236,72,153,0.18)" />
            <path d="M 80 -24 L 92 -34 L 92 14 L 80 24 Z" fill="rgba(251,191,36,0.18)" />
            <text x="5" y="-8" fill="#a78bfa" opacity="0.65" fontSize="9" fontFamily="monospace">address</text>
            <text x="5" y="2" fill="#a78bfa" opacity="0.65" fontSize="9" fontFamily="monospace">indexed</text>
            <text x="5" y="12" fill="#a78bfa" opacity="0.65" fontSize="9" fontFamily="monospace">user)</text>
          </g>
        </g>

        {/* Mycelial web connections - INCREASED VISIBILITY - adjusted for taller hero */}
        {/* Top row connections */}
        <path d="M 510 30 Q 520 25, 590 30" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />
        <path d="M 610 30 Q 620 25, 690 30" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />
        <path d="M 710 30 Q 720 25, 790 30" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />
        <path d="M 810 30 Q 820 25, 890 30" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />

        {/* Bottom row connections */}
        <path d="M 510 570 Q 520 575, 590 570" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />
        <path d="M 610 570 Q 620 575, 690 570" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />
        <path d="M 710 570 Q 720 575, 790 570" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />
        <path d="M 810 570 Q 820 575, 890 570" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />

        {/* Left side connections */}
        <path d="M 150 270 Q 145 300, 150 330" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />

        {/* Right side connections */}
        <path d="M 1250 270 Q 1255 300, 1250 330" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.6" fill="none" strokeDasharray="4 3" />

        {/* Corner connections - completing the loop */}
        <path d="M 300 30 Q 220 150, 150 270" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.5" fill="none" strokeDasharray="4 3" />
        <path d="M 1100 30 Q 1180 150, 1250 270" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.5" fill="none" strokeDasharray="4 3" />
        <path d="M 150 330 Q 220 450, 300 570" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.5" fill="none" strokeDasharray="4 3" />
        <path d="M 1250 330 Q 1180 450, 1100 570" stroke="url(#heroChainGrad)" strokeWidth="2.2" opacity="0.5" fill="none" strokeDasharray="4 3" />

        {/* Spider web-like cross connections for network effect - MORE VISIBLE */}
        <path d="M 500 30 Q 350 180, 250 300" stroke="#7c3aed" strokeWidth="1.5" opacity="0.4" fill="none" strokeDasharray="2 4" />
        <path d="M 900 30 Q 1050 180, 1150 300" stroke="#ec4899" strokeWidth="1.5" opacity="0.4" fill="none" strokeDasharray="2 4" />
        <path d="M 250 300 Q 350 420, 500 570" stroke="#fbbf24" strokeWidth="1.5" opacity="0.4" fill="none" strokeDasharray="2 4" />
        <path d="M 1150 300 Q 1050 420, 900 570" stroke="#a78bfa" strokeWidth="1.5" opacity="0.4" fill="none" strokeDasharray="2 4" />

        {/* Subtle connective waves behind title - centered at y=400 */}
        <path d="M 100 400 Q 350 340, 700 400 T 1300 400" stroke="url(#heroChainGrad)" strokeWidth="2" fill="none" opacity="0.28">
          <animate attributeName="d" dur="6s" repeatCount="indefinite"
            values="M 100 400 Q 350 340, 700 400 T 1300 400; M 100 400 Q 350 360, 700 400 T 1300 400; M 100 400 Q 350 340, 700 400 T 1300 400" />
        </path>

        {/* Ceremonial circle around title - sacred geometry */}
        <defs>
          <linearGradient id="ceremonialGrad" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" stopColor="#7c3aed" stopOpacity="0.4" />
            <stop offset="33%" stopColor="#ec4899" stopOpacity="0.35" />
            <stop offset="66%" stopColor="#fbbf24" stopOpacity="0.4" />
            <stop offset="100%" stopColor="#7c3aed" stopOpacity="0.4" />
          </linearGradient>
        </defs>
        {/* Outer ceremonial ring - breathing animation */}
        <circle cx="700" cy="400" r="200" stroke="url(#ceremonialGrad)" strokeWidth="1.5" fill="none" opacity="0.35">
          <animate attributeName="r" values="200;210;200" dur="8s" repeatCount="indefinite" />
          <animate attributeName="opacity" values="0.35;0.45;0.35" dur="8s" repeatCount="indefinite" />
        </circle>
        {/* Inner ceremonial ring - counter-breathing */}
        <circle cx="700" cy="400" r="160" stroke="url(#ceremonialGrad)" strokeWidth="1" fill="none" opacity="0.25" strokeDasharray="8 4">
          <animate attributeName="r" values="160;150;160" dur="8s" repeatCount="indefinite" />
          <animate attributeName="stroke-dashoffset" values="0;24" dur="12s" repeatCount="indefinite" />
        </circle>
        {/* Sacred geometry - rotating hexagon points */}
        <g transform="translate(700, 400)" opacity="0.3">
          <animateTransform attributeName="transform" type="rotate" from="0 0 0" to="360 0 0" dur="60s" repeatCount="indefinite" additive="sum" />
          {[0, 60, 120, 180, 240, 300].map((angle, i) => (
            <circle
              key={i}
              cx={Math.cos(angle * Math.PI / 180) * 180}
              cy={Math.sin(angle * Math.PI / 180) * 180}
              r="4"
              fill={['#7c3aed', '#ec4899', '#fbbf24', '#a78bfa', '#f472b6', '#fcd34d'][i]}
              opacity="0.6"
            >
              <animate attributeName="r" values="4;6;4" dur="3s" begin={`${i * 0.5}s`} repeatCount="indefinite" />
            </circle>
          ))}
        </g>
      </svg>
    </div>
  );
}

